.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (max-width: 62em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
    /* 992px */
  }
}

.card {
  width: 57.5rem;
  height: 31.8125rem;
  border-radius: 1.375rem;
  background-color: white;
  display: flex;
  flex-direction: row;
}
.card-accordion {
  width: 50%;
  padding: 43px 95px 43px 17px;
  font-size: 12px;
  font-family: "Kumbh Sans", sans-serif;
}
.card-accordion > h1 {
  padding: 28px 0px 28px 0px;
}
.card-accordion > button {
  width: 100%;
  padding: 20px 0px 20px 0px;
  background-color: white;
  border: none;
  text-align: start;
}
.card-accordion > div {
  display: none;
}
.card-accordion > hr {
  color: #e7e7e9;
}
.card-image {
  background: no-repeat url("../../../images/bg-pattern-desktop.svg");
  background-position: top 50% left 50%;
  background-size: contain;
}

.bg {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(#6565e7, #af67e9);
  display: flex;
  justify-content: center;
  align-items: center;
}